export default function CreateUser() {
  const [province, setProvince] = useState('');
  const [canton, setCanton] = useState('');
  const [distrit, setDistrit] = useState('');

  const cantonOptions = cantonsByProvince[province] || [];
  const distritOptions = distritsByCanton[province]?.[canton] || [];



  const classes = useStyles();

  const history = useHistory();

  // Estado para los nuevos atributos
  const [identification, setIdentification] = useState('');
  const [identificationType, setIdentificationType] = useState('');
  const [name, setName] = useState('');
  const [lastname, setLastname] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [phone, setPhone] = useState('');
  const [birthday, setBirthday] = useState('');
  const [gender, setGender] = useState('');
  const [passwordError, setPasswordError] = useState('');
  const [emailError, setEmailError] = useState('');
  const [phoneError, setPhoneError] = useState('');

  const [confirmPassword, setConfirmPassword] = useState('');



  const [confirmPasswordError, setConfirmPasswordError] = useState('');
  const [nameError, setNameError] = useState('');
  const [lastnameError, setLastnameError] = useState('');
  const [identificationError, setIdentificationError] = useState('');
  const [identificationTypeError, setIdentificationTypeError] = useState('');
  const [genderError, setGenderError] = useState('');
  const [provinceError, setProvinceError] = useState('');
  const [cantonError, setCantonError] = useState('');
  const [distritError, setDistritError] = useState('');
  const [birthdayError, setBirthdayError] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);

  const identificationRegex = /^\d+$/;
  const nameRegex = /^[A-Z][a-z]*$/;
  const lastnameRegex = /^[A-Z][a-z]*$/;
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  const phoneRegex = /^\d+$/;

  const handleGenderChange = (e) => {
    const value = e.target.value;
    setGender(value);
    if (!value) {
      setGenderError('Este campo es obligatorio');
    } else {
      setGenderError('');
    }
  }

  const handleIdentificationTypeChange = (e) => {
    const value = e.target.value;
    setIdentificationType(value);
    if (!value) {
      setIdentificationTypeError('El tipo de identificacin es obligatorio');
    } else {
      setIdentificationTypeError('');
    }
  }

  const handleProvinceChange = (e) => {
    const value = e.target.value;
    setProvince(value);
    if (!value) {
      setProvinceError('La provincia es obligatoria');
    } else {
      setProvinceError('');
    }
  }

  const handleCantonChange = (e) => {
    const value = e.target.value;
    setCanton(value);
    if (!value) {
      setCantonError('El cantn es obligatorio');
    } else {
      setCantonError('');
    }
  }

  const handledistritChange = (e) => {
    const value = e.target.value;
    setDistrit(value);
    if (!value) {
      setDistritError('El distrito es obligatorio');
    } else {
      setDistritError('');
    }
  }
  const handleDateChange = (event) => {
    const selectedDate = event.target.value;
    setBirthday(selectedDate);
    if (selectedDate) {
      setBirthdayError('');
    }
  };


  const handleSubmit = async (e) => {

    e.preventDefault();

    let isValid = true;
    setIsSubmitting(true);



    if (!birthday) {
      setBirthdayError('La fecha de nacimiento es obligatoria');
      isValid = false;
    } else {
      setBirthdayError(''); // Reinicia el mensaje de error si el campo es vlido
    }

    if (!province) {
      setProvinceError('La provincia es obligatoria');
      isValid = false;
    } else {
      setProvinceError(''); // Reinicia el mensaje de error si el campo es vlido
    }

    if (!distrit) {
      setDistritError('El cantn es obligatorio');
      isValid = false;
    } else {
      setDistritError(''); // Reinicia el mensaje de error si el campo es vlido
    }


    if (!canton) {
      setCantonError('El distrito es obligatorio');
      isValid = false;
    } else {
      setCantonError('');
    }

    if (!identificationType) {
      setIdentificationTypeError('El tipo de identificacin es obligatorio');
      isValid = false;
    } else {
      setIdentificationTypeError('');
    }


    if (!identificationRegex.test(identification) || identification.length < 9) {
      setIdentificationError('La identificacin debe contener al menos 10 dgitos y solo nmeros.');
      isValid = false;
    } else {
      setIdentificationError('');
    }




    if (!nameRegex.test(name)) {
      setNameError('El nombre debe comenzar con una letra mayscula y contener solo letras minsculas.');
      isValid = false;
    } else {
      setNameError('');
    }

    // Validacin de apellido (solo letras, primera letra en mayscula)

    if (!lastnameRegex.test(lastname)) {
      setLastnameError('El apellido debe comenzar con una letra mayscula y contener solo letras minsculas.');
      isValid = false;
    } else {
      setLastnameError('');
    }

    if (
      password.length < 8 || // Mnimo 8 caracteres
      !/[A-Za-z]/.test(password) || // Al menos una letra
      !/\d/.test(password) || // Al menos un nmero
      !/[!@#$%^&*(),.?":{}|<>]/.test(password) // Al menos un carcter especial
    ) {
      setPasswordError('La contrasea debe tener al menos 8 caracteres y contener al menos una letra, un nmero y un carcter especial.');
      isValid = false;

    } else {
      setPasswordError('');
    }


    if (!emailRegex.test(email)) {
      setEmailError('El formato del correo electrnico no es vlido.');
      isValid = false;
    } else {
      setEmailError('');
    }


    if (!phoneRegex.test(phone) || phone.length < 7) { // Cambio en la validacin
      setPhoneError('El nmero de telfono debe contener al menos 8 dgitos y solo nmeros.');
      isValid = false;
    } else {
      setPhoneError('');
    }

    if (!gender) {
      setGenderError('Este campo es obligatorio');
      isValid = false;
      return;
    }


    if (confirmPassword.trim() === '') {
      setConfirmPasswordError('El campo de confirmacin de contrasea est vaco');
      isValid = false;
    } else if (confirmPassword !== password) {
      setConfirmPasswordError('Las contraseas no coinciden');
      isValid = false;
    } else {
      setConfirmPasswordError(''); // Borra el mensaje de error si las contraseas coinciden
    }

    if (!province) {
      setProvinceError('Este campo es obligatorio');
      isValid = false;
    } else {
      setProvinceError(''); // Reinicia el mensaje de error si el campo es vlido
    }


    if (isValid) {
      axios
        .post('https://api.clinicadentalsofiacastro.com/register', {
          name,
          lastname,
          email,
          password,
          identification,
          phone,
          birthday,
          gender,
          province,
          canton,
          distrit,
          identificationType,
          confirmPassword,
        })
        .then((result) => {
          console.log(result);
          if (result.data === 'Usuario creado') {
            history.push('/Admin/Dashboard');
            Swal.fire({
              title: 'Exitoso',
              text: 'Agregado exitosamente',
              icon: 'success',
            });
          } else if (result.data === 'Hay campos vacios') {
            Swal.fire({
              title: 'Error',
              text: 'Por favor llene todos los campos',
              icon: 'error',
            });
          } else if (result.data === 'El correo y la identificacin ya existen') {
            Swal.fire({
              title: 'Error',
              text: 'Ya un usuario existe con este correo y identificacion!',
              icon: 'error',
            });
          } else if (result.data === 'El correo ya existe') {
            Swal.fire({
              title: 'Error',
              text: 'Ya un usuario existe con este correo!',
              icon: 'error',
            });
          } else if (result.data === 'La identificacin ya existe') {
            Swal.fire({
              title: 'Error',
              text: 'Ya un usuario existe con esta identificacion!',
              icon: 'error',
            });
          }
        })
        .catch((err) => console.log(err));
    }
  };


  return (
    <div style={bgStyle}>
      <Container component="main" style={{ maxWidth: 600 }}>
        <CssBaseline />
        <div className={classes.paper}>
          <Avatar className={classes.avatar}></Avatar>
          <Typography component="h1" variant="h5" style={txtWhite}>
            Crear Usuario
          </Typography>

          <form onSubmit={handleSubmit} className={classes.form} noValidate>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <FormControl variant="filled" fullWidth style={tfStyle}>
                  <InputLabel id="identificationType-label">Seleccione tipo de Identificacion</InputLabel>
                  <Select
                    labelId="identificationType-label"
                    id="identificationType"
                    value={identificationType}
                    onChange={handleIdentificationTypeChange}
                    label="IdentificationType"
                  >
                    <MenuItem value="Fisica">Nacional</MenuItem>
                    <MenuItem value="Pasaporte">Pasaporte</MenuItem>
                    <MenuItem value="Documento Migratorio">Documento Migratorio</MenuItem>

                  </Select>
                </FormControl>
                {isSubmitting && !identificationType && (
                  <div style={{
                    textAlign: 'left',
                    color: '#f2a4a4',
                    fontSize: '15px',
                    fontWeight: 'normal',
                    borderRadius: '5px',
                    marginTop: '4px',
                    marginBottom: '4px',
                  }}>
                    <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                      paddingTop: '1px',
                      paddingRight: '4px',
                      width: '15px',
                    }} />
                    {identificationTypeError}
                  </div>
                )}
              </Grid>
              <Grid item xs={12}>
                <Textfield
                  style={tfStyle}
                  variant="filled"
                  fullWidth
                  id="identification"
                  label="Nmero de Identificacin"
                  onChange={(e) => {
                    const value = e.target.value;
                    setIdentification(value);
                    if (identificationRegex.test(value)) {
                      setIdentificationError(''); // Clear the error message
                    }
                  }}
                  name="identification"
                />
                {identificationError && (
                  <div style={{
                    textAlign: 'left',
                    color: '#f2a4a4',
                    fontSize: '15px',
                    fontWeight: 'normal',
                    borderRadius: '5px',
                    marginTop: '4px',
                    marginBottom: '4px',
                  }}>
                    <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                      paddingTop: '1px',
                      paddingRight: '4px',
                      width: '15px',
                    }} />
                    {identificationError}
                  </div>
                )}
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  style={tfStyle}
                  autoComplete="fname"
                  name="firstName"
                  variant="filled"
                  required
                  fullWidth
                  id="firstName"
                  label="Primer Nombre"
                  onChange={(e) => {
                    const value = e.target.value;
                    setName(value);
                    if (nameRegex.test(value)) {
                      setNameError(''); // Clear the error message
                    }
                  }}
                  autoFocus
                />
                {nameError && (
                  <div style={{
                    textAlign: 'left',
                    color: '#f2a4a4',
                    fontSize: '15px',
                    fontWeight: 'normal',
                    borderRadius: '5px',
                    marginTop: '4px',
                    marginBottom: '4px',
                  }}>
                    <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                      paddingTop: '1px',
                      paddingRight: '4px',
                      width: '15px',
                    }} />
                    {nameError}
                  </div>
                )}
              </Grid>

              <Grid item xs={12} sm={6}>
                <TextField
                  style={tfStyle}
                  variant="filled"
                  required
                  fullWidth
                  id="lastName"
                  label="Apellido"
                  name="lastName"
                  onChange={(e) => {
                    const value = e.target.value;
                    setLastname(value);
                    if (lastnameRegex.test(value)) {
                      setLastnameError(''); // Clear the error message
                    }
                  }}
                  autoComplete="lname"
                />
                {lastnameError && (
                  <div style={{
                    textAlign: 'left',
                    color: '#f2a4a4',
                    fontSize: '15px',
                    fontWeight: 'normal',
                    borderRadius: '5px',
                    marginTop: '4px',
                    marginBottom: '4px',
                  }}>
                    <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                      paddingTop: '1px',
                      paddingRight: '4px',
                      width: '15px',
                    }} />
                    {lastnameError}
                  </div>
                )}
              </Grid>

              <Grid item xs={12}>
                <TextField
                  style={tfStyle}
                  variant="filled"
                  required
                  fullWidth
                  id="email"
                  label="Correo"
                  name="email"
                  onChange={(e) => {
                    const value = e.target.value;
                    setEmail(value);
                    if (emailRegex.test(value)) {
                      setEmailError(''); // Clear the error message
                    }
                  }}
                  autoComplete="email"
                />
                {emailError && (
                  <div style={{
                    textAlign: 'left',
                    color: '#f2a4a4',
                    fontSize: '15px',
                    fontWeight: 'normal',
                    borderRadius: '5px',
                    marginTop: '4px',
                    marginBottom: '4px',
                  }}>
                    <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                      paddingTop: '1px',
                      paddingRight: '4px',
                      width: '15px',
                    }} />
                    {emailError}
                  </div>
                )}
              </Grid>

              <Grid item xs={12}>
                <TextField
                  style={tfStyle}
                  variant="filled"
                  required
                  fullWidth
                  name="password"
                  label="Contrasea"
                  type="password"
                  id="password"
                  value={password}
                  onChange={(e) => {
                    const value = e.target.value;
                    setPassword(value);
                    if (value.length >= 8 && /[A-Za-z]/.test(value) && /\d/.test(value) && /[!@#$%^&*(),.?":{}|<>]/.test(value)) {
                      setPasswordError(''); // Borra el mensaje de error si la contrasea es vlida
                    } else {
                      setPasswordError('La contrasea debe contener al menos 8 caracteres, una letra, un nmero y un carcter especial.');
                    }
                  }}
                  autoComplete="current-password"
                />
                {passwordError && (
                  <div style={{
                    textAlign: 'left',
                    color: '#f2a4a4',
                    fontSize: '15px',
                    fontWeight: 'normal',
                    borderRadius: '5px',
                    marginTop: '4px',
                    marginBottom: '4px',
                  }}>
                    <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                      paddingTop: '1px',
                      paddingRight: '4px',
                      width: '15px',
                    }} />
                    {passwordError}
                  </div>
                )}
              </Grid>
              <Grid item xs={12}>
                <TextField
                  style={tfStyle}
                  variant="filled"
                  required
                  fullWidth
                  name="confirmPassword"
                  label="Confirmar Contrasea"
                  type="password"
                  id="confirmPassword"
                  value={confirmPassword}
                  onChange={(e) => {
                    const value = e.target.value;
                    setConfirmPassword(value);
                    if (value === password) {
                      setConfirmPasswordError(''); // Borra el mensaje de error si la confirmacin coincide con la contrasea
                    } else {
                      setConfirmPasswordError('Las contraseas no coinciden');
                    }
                  }}
                />
                {confirmPasswordError && (
                  <div style={{
                    textAlign: 'left',
                    color: '#f2a4a4',
                    fontSize: '15px',
                    fontWeight: 'normal',
                    borderRadius: '5px',
                    marginTop: '4px',
                    marginBottom: '4px',
                  }}>
                    <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                      paddingTop: '1px',
                      paddingRight: '4px',
                      width: '15px',
                    }} />
                    {confirmPasswordError}
                  </div>
                )}
              </Grid>
              <Grid item xs={12}>
                <TextField
                  style={tfStyle}
                  variant="filled"
                  fullWidth
                  id="phone"
                  label="Nmero de Telfono"
                  onChange={(e) => {
                    const value = e.target.value;
                    setPhone(value);
                    if (phoneRegex.test(value)) {
                      setPhoneError(''); // Clear the error message
                    }
                  }}
                  name="phone"
                />
                {phoneError && (
                  <div style={{
                    textAlign: 'left',
                    color: '#f2a4a4',
                    fontSize: '15px',
                    fontWeight: 'normal',
                    borderRadius: '5px',
                    marginTop: '4px',
                    marginBottom: '4px',
                  }}>
                    <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                      paddingTop: '1px',
                      paddingRight: '4px',
                      width: '15px',
                    }} />
                    {phoneError}
                  </div>
                )}
              </Grid>

              <Grid item xs={12}>
                <Textfield
                  style={tfStyle}
                  variant="filled"
                  type="date"
                  fullWidth
                  id="birthday"
                  label="Fecha de Nacimiento"
                  InputLabelProps={{
                    shrink: true,
                  }}
                  name="birthday"
                  value={birthday}
                  onChange={handleDateChange}
                />
                {isSubmitting && birthdayError && (
                  <div style={{
                    textAlign: 'left',
                    color: '#f2a4a4',
                    fontSize: '15px',
                    fontWeight: 'normal',
                    borderRadius: '5px',
                    marginTop: '4px',
                    marginBottom: '4px',
                  }}>
                    <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                      paddingTop: '1px',
                      paddingRight: '4px',
                      width: '15px',
                    }} />
                    {birthdayError}
                  </div>
                )}
              </Grid>
              <Grid item xs={12}>
                <FormControl variant="filled" fullWidth style={tfStyle}>
                  <InputLabel id="gender-label">Gnero</InputLabel>
                  <Select
                    labelId="gender-label"
                    id="gender"
                    value={gender}
                    label="Gnero"
                    onChange={handleGenderChange}
                  >
                    <MenuItem value="No especificado">No especificar</MenuItem>
                    <MenuItem value="Masculino">Masculino</MenuItem>
                    <MenuItem value="Femenino">Femenino</MenuItem>
                  </Select>
                </FormControl>
                {isSubmitting && !gender && (
                  <div style={{
                    textAlign: 'left',
                    color: '#f2a4a4',
                    fontSize: '15px',
                    fontWeight: 'normal',
                    borderRadius: '5px',
                    marginTop: '4px',
                    marginBottom: '4px',
                  }}>
                    <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                      paddingTop: '1px',
                      paddingRight: '4px',
                      width: '15px',
                    }} />
                    {genderError}
                  </div>
                )}
              </Grid>
              <Grid container spacing={2}>
                {/* Dropdown de Provincia */}
                <Grid item xs={4}>
                  <FormControl variant="filled" fullWidth style={tfStyle}>
                    <InputLabel id="province-label">Provincia</InputLabel>
                    <Select
                      labelId="province-label"
                      id="province"
                      value={province}
                      label="Provincia"
                      onChange={handleProvinceChange}
                    >
                      <MenuItem value="">Seleccionar provincia</MenuItem>
                      {provinces.map((prov) => (
                        <MenuItem key={prov} value={prov}>
                          {prov}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                  {isSubmitting && !province && (
                    <div style={{
                      textAlign: 'left',
                      color: '#f2a4a4',
                      fontSize: '15px',
                      fontWeight: 'normal',
                      borderRadius: '5px',
                      marginTop: '4px',
                      marginBottom: '4px',
                    }}>
                      <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                        paddingTop: '1px',
                        paddingRight: '4px',
                        width: '15px',
                      }} />
                      {provinceError}
                    </div>
                  )}
                </Grid>
                {/* Dropdown de Cantn */}
                <Grid item xs={4}>
                  <FormControl variant="filled" fullWidth style={tfStyle}>
                    <InputLabel id="canton-label">Cantn</InputLabel>
                    <Select
                      labelId="canton-label"
                      id="canton"
                      value={canton}
                      onChange={handleCantonChange}
                      label="Cantn"
                      disabled={!province} // Deshabilitar si no se ha seleccionado una provincia
                    >
                      <MenuItem value="">Seleccionar cantn</MenuItem>
                      {cantonOptions.map((can) => (
                        <MenuItem key={can} value={can}>
                          {can}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                  {isSubmitting && !canton && (
                    <div style={{
                      textAlign: 'left',
                      color: '#f2a4a4',
                      fontSize: '15px',
                      fontWeight: 'normal',
                      borderRadius: '5px',
                      marginTop: '4px',
                      marginBottom: '4px',
                    }}>
                      <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                        paddingTop: '1px',
                        paddingRight: '4px',
                        width: '15px',
                      }} />
                      {cantonError}
                    </div>
                  )}
                </Grid>
                {/* Dropdown de Distrito */}
                <Grid item xs={4}>
                  <FormControl variant="filled" fullWidth style={tfStyle}>
                    <InputLabel id="distrit-label">Distrito</InputLabel>
                    <Select
                      labelId="distrit-label"
                      id="distrit"
                      value={distrit}
                      onChange={handledistritChange}
                      label="Distrito"
                      disabled={!canton} // Deshabilitar si no se ha seleccionado un cantn
                    >
                      <MenuItem value="">Seleccionar distrito</MenuItem>
                      {distritOptions.map((dist) => (
                        <MenuItem key={dist} value={dist}>
                          {dist}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                  {isSubmitting && !distrit && (
                    <div style={{
                      textAlign: 'left',
                      color: '#f2a4a4',
                      fontSize: '15px',
                      fontWeight: 'normal',
                      borderRadius: '5px',
                      marginTop: '4px',
                      marginBottom: '4px',
                    }}>
                      <img src="https://cdn0.iconfinder.com/data/icons/shift-interfaces/32/Error-512.png" style={{
                        paddingTop: '1px',
                        paddingRight: '4px',
                        width: '15px',
                      }} />
                      {distritError}
                    </div>
                  )}
                </Grid>
              </Grid>
              {/* Fin de los nuevos campos */}

            </Grid>
            {/* {error && <div className={styles.error_msg}>{error}</div>} */}
            <Button
              onClick={handleSubmit}
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
            >
              Registrarse
            </Button>
            
          </form>
        </div>
        <Box mt={6}></Box>
      </Container>
    </div>
  );
}
