import React from 'react';
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Title from './Title';

// Generate Order Data
function createData(id, date, name, paymentMethod, amount) {
  return { id, date, name, paymentMethod, amount };
}

const rows = [
  createData(0, '16 Mar, 2023', 'Elvis Presley', 'VISA  3719', '$ 312.44'),
  createData(1, '16 Mar, 2023', 'Paul McCartney', 'VISA  2574', '$ 866.99'),
  createData(2, '16 Mar, 2023', 'Tom Scholz', 'MC  1253', '$ 100.81'),
  createData(3, '16 Mar, 2023', 'Michael Jackson', 'AMEX  2000', '$ 654.39'),
  createData(4, '15 Mar, 2023', 'Bruce Springsteen', 'VISA  5919', '$ 212.79'),
];

function preventDefault(event) {
  event.preventDefault();
}

const useStyles = makeStyles((theme) => ({
  seeMore: {
    marginTop: theme.spacing(3),
  },
}));

export default function Orders() {
  const classes = useStyles();
  return (
    <React.Fragment>
      <Title>Citas Recientes</Title>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>Fecha</TableCell>
            <TableCell>Nombre</TableCell>
            {/*<TableCell>Ship To</TableCell>*/}
            <TableCell>Mtodo de Pago</TableCell>
            <TableCell align="right">Monto</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.id}>
              <TableCell>{row.date}</TableCell>
              <TableCell>{row.name}</TableCell>
              {/*<TableCell>{row.shipTo}</TableCell>*/}
              <TableCell>{row.paymentMethod}</TableCell>
              <TableCell align="right">{row.amount}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <div className={classes.seeMore}>
        <Link color="primary" href="#" onClick={preventDefault}>
          Ver ms citas
        </Link>
      </div>
    </React.Fragment>
  );
}